/******  Variables *********/

:root {
  --color-mustard-yellow: #DEA73A;
  --color-blue: #1C2B65;
  --color-light-blue: #50C4FB;
  --color-night-blue: #02234E;
  --color-light-grey: #DDDDDD;
  --color-dark-grey: #666666;
  --color-charcoal: #323232;
  --color-black: #272727;

  --font-heading: "Montserrat", sans-serif;
  --font-body: "Open Sans", sans-serif;

  --side-margins: 10vw;
  --nav-height: 80px;
}


/****** Animation keyframes *********/

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}


/******  General Styles ********/

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-family: var(--font-body);
  color: #323232;
  color: var(--color-charcoal);
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-heading);
  margin: 0;
}

h1 {
  font-size: 42px;
  font-weight: 500;
  padding: 30px 10px 30px 0;
}

h1.trip-title {
  padding: 0;
}

h2 {
  padding-top: 30px;
  padding-bottom: 20px;
  font-size: 30px;
  font-weight: 500;
  color: #1C2B65;
  color: var(--color-blue);
  text-transform: uppercase;
}

h3 {
  padding-top: 20px;
  font-size: 20px;
  font-weight: 500;
  color: #1C2B65;
  color: var(--color-blue);
}

p {
  font-family: "Open Sans", sans-serif;
  font-family: var(--font-body);
  line-height: 1.5em;
}
p.hero-text {
  color: #DDDDDD;
  color: var(--color-light-grey);
  margin-top: 30px;
  margin-bottom: 30px;
}
p.video-button {
  padding: 20px;
  text-align: center;
  color: #323232;
  color: var(--color-charcoal);
  background-color: #50C4FB;
  background-color: var(--color-light-blue);
  visibility: hidden;
}

ul {
  padding: 0;
  padding-left: 20px;
  /* list-style: circle; */
}
ul.no-style {
  padding: 0;
  list-style: none;
}
ul.active-style {
  padding: 0;
  list-style: disc;
}
ul.no-style-light-blue {
  padding: 0;
  color: #50C4FB;
  color: var(--color-light-blue);
  list-style: none;
}


li {
  line-height: 1.5em;
  padding-bottom: 10px;
  padding-top: 10px;
}
li.hero-inline-list_item {
  display: inline;
}
li.hero-inline-list_item:after {
  content: "   |   ";
}
li.hero-inline-list_item:last-child:after {
  content: "";
}


a {
  text-decoration: none;
  text-underline-position: under;
}
a:link, a:visited {
  color: inherit;
}
a:hover {
  color: #1C2B65;
  color: var(--color-blue);
  transition-duration: 200ms;
}
a:active!important {
  color: #1C2B65;
  color: var(--color-blue);
  text-decoration: underline;
}
a.country-link {
  text-decoration: underline;
}

hr.style-1 {
    border: 0;
    height: 0;
    margin-top: 30px;
    margin-bottom: 30px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
.line {
  width: 100%;
  flex: 1 1;
  border-bottom: 1px;
  border-width: thin;
}



/***** Base Layout Styles *******/

.content-container {
  margin-left: 10vw;
  margin-left: var(--side-margins);
  margin-right: 10vw;
  margin-right: var(--side-margins);
  padding: 0;
}

.page-header {
  display: flex;
  padding-top: 40px;
  padding-bottom: 40px;
  justify-content: center;
}
.page-header h1 {
  font-size: 30px;
  /* text-decoration: underline; */
  text-underline-position: under;
}

.content-section {
  margin-top: 40px;
  margin-bottom: 40px;
  width: 70%;
}
.content-section .blog img {
  max-height: calc(80vh);
  max-width: 100%;
}

.content-section-header {
  display: flex;
  width: 100%;
}

.content-section-header__text {
  flex: 1 1
}

.icon {
  padding-right: 20px;
}

.icon-after {
  padding-left: 10px;
}

:target::before {
  content: "";
  display: block;
  height: 80px; /* fixed header height*/
  margin: -80px 0 0; /* negative fixed header height */
}


/******  Navigation Style ********/

.nav {
  height: 80px;
  height: var(--nav-height);
  box-sizing: border-box;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
  justify-content: center;
  align-items: center;
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-heading);
  background-color: #FFFFFF;
  color: #666666;
  color: var(--color-dark-grey);
  border-bottom: 1px solid rgba(80,80,80,0.2);
}
.menu {
  visibility: visible;
}
.nav-active {
  color: #1C2B65 !important;
  color: var(--color-blue) !important;
  font-weight: 600;
}
.nav ul a {
  list-style: none;
  text-decoration: none;
  margin: 0;
  padding: 0;
  color: inherit;
}
.nav li {
  display: inline;
  margin: 0;
  padding-left: 15px;
  padding-right: 15px;
}
.nav a:hover {
  color: #1C2B65;
  color: var(--color-blue);
  font-weight: 500;
}

.nav-hamburger {
  position: absolute;
  top: 0;
  right: 40px;
  display: flex;
  visibility: hidden;
}


#side-nav-left {
  display: flex;
  position: fixed;
  top: 80px;
  top: var(--nav-height);
  left: 0;
  top: 245px;
  width: 10vw;
  width: var(--side-margins);
  /* padding: 40px; */
  justify-content: center;
  color: #666666;
  color: var(--color-dark-grey);
  /* visibility: hidden; */
}
#side-nav-left a.active {
  font-weight:700;
  color: #50C4FB;
  color: var(--color-light-blue);
}
#side-nav-left a:hover {
  color: #50C4FB;
  color: var(--color-light-blue);
}


#logo {

  position: absolute;
  left: 40px;
  top: 20px;
  margin-top: 10px;
  font-weight: 500;
  font-size: 20px;
}

#logo .icon {
  color: #1C2B65;
  color: var(--color-blue);
}


/****** Homepage Style ********/

.hero-image-main {
  display: flex;
  width: 100%;
  position: absolute;
  top: 80px;
  top: var(--nav-height);
  left: 0;
  bottom: 0;
  justify-content: center;
  background-size: cover;
  background-position: center;
}
.hero-image-main:after {
  width: 100%;
  height: 100%;
  background-color: black;

}
.title-area {
  display: block;
  height: 250px;
  width: 40%;
  margin-top: 2%;
  margin-bottom: 60px;
  padding: 40px;
  padding-bottom: 0;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-heading);
  color: #1a1e0c;
  /* background-color: rgba(80,80,80,0.4); */
  /* border: 1px solid white; */
  /* visibility: hidden; */
}
.title-area h1 {
  font-size: 60px;
  padding-bottom: 20px;
  opacity: 0;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.title-area p {
  margin: 0;
  opacity: 0;
  font-size: 30px;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
.title-cta {
  height: 50px;
  width: 200px;
  opacity: 0;
  margin-top: 60px;
  color: #323232;
  color: var(--color-charcoal);
  font-size: 16px;
  background: none;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  box-shadow: none;
  border: 1px solid #323232;
  border: 1px solid var(--color-charcoal);
}
.title-cta:hover {
  background-color: rgba(200,200,200,0.2);
  cursor: pointer;

}


/***** Tile Styles ********/

.tiles {
  display: grid;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  /* overflow-y: visible; */
}

.tile {
  width: 100%;
  color: white;
  /* max-width: 40vw; */
  /* height: 25vh; */
  height: 250px;
  max-height: 250px;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.4);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: hue;

}
.below-fixed-header::before {
  content: " ";
  display: block;
  height: 80px;
  height: var(--nav-height); /* fixed header height*/
  margin: calc(0px - 80px) 0 0;
  margin: calc(0px - var(--nav-height)) 0 0; /* negative fixed header height */
  visibility: hidden;
}
.tile:hover {
  background-color: rgba(0,0,0,0.1);
  background-blend-mode: hue;
  transition-duration: 200ms;
}

.filtered {
  -webkit-filter: blur(5px);
  /* -webkit-filter: grayscale(1); */
}

.tile-text {
  color: inherit;
  height: 100%;
  width: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;

  /* visibility:hidden; */
}

.tile-text h2 {
  margin: 0;
  padding: 10px;
  font-size: 26px;
  font-weight: 400;
  color: inherit;
}


/****** Country Style ********/

.country-hero {
  display: flex;
  width: 100%;
  height: 85vh;
  min-height: 500px;
  margin-bottom: 20px;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.3);
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: overlay;
}

.country-hero-text {
  color: #FFFFFF;
  font-size: 72px;
  line-height: 3em;
  padding-left: 40px;
  padding-right: 40px;
  text-transform: uppercase;
  /* background-color: var(--color-charcoal); */
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-heading);
  font-weight: 500;
  /* text-shadow: 2px 2px 3px black; */
}

.country-content {
  margin-left: 15vw;
  margin-right: 15vw;
  padding: 40px 0 40px 0;
}


/****** Trip Style ********/

.trip-hero {
  display: grid;
  grid-template-columns: 1fr 3fr;
  width: 100%;
  height: 85vh;
  min-height: 500px;
  margin-bottom: 20px;

}
.trip-hero-text {
  flex: 1 1;
  padding: 50px 40px 30px 40px;
  color: #FFFFFF;
  background-color: #272727;
  background-color: var(--color-black);
  font-family: "Montserrat", sans-serif;
  font-family: var(--font-heading);
}
.trip-hero-text a:hover {
  color: #FFFFFF;
}
/* .trip-hero-text__title {
  height: 150px;
  display: flex;
  margin: -20px -30px 0 -30px;
  padding-left: 30px;
  padding-top: 35px;
  background-color: black;
} */

.country-link {
  color: inherit;
}

.hero-image {
  flex: 3 1;
  background-size: cover;
  background-position: center;
}

.content-grid {
  display: flex;
}

.col-left {
  flex: 1 1;
  width: 50%;
  margin-right: 20px;
  box-sizing: border-box;
}

.col-right {
  flex: 2 1;
  width: 50%;
  height: 100%;
  margin-left: 50px;
  box-sizing: border-box;
  background-color: #666666;
  background-color: #323232;
  background-color: var(--color-charcoal);
}


/***** Carousel Style ********/

.BrainhubCarousel {
  width: 100%;
}

.BrainhubCarousel img{
  min-height: 100%
}

.BrainhubCarouselItem {
  overflow: hidden;
}

.thumbnail{
  height: 30px;
}

.carousel-arrow {
  /* color: var(--color-charcoal); */
  height: 400px;
  padding-left: 40px;
  padding-right: 40px;
  color: white;
  background-color: #323232;
  background-color: var(--color-charcoal);
}


.trip-content-details {
  padding: 40px 20px 40px 20px;
  width: 70%;
}

.trip-content-details img {
  width: 100%;
}




.map {
  width: 100vw;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: 80px;
  margin-top: var(--nav-height);
  padding-bottom: 80px;
  padding-bottom: var(--nav-height);
  box-sizing: border-box;
  border-style: none;
}

.map > iframe {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

#footer {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  display: flex;
  padding-top: 10px;
  padding-bottom: 10px;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  visibility: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

